@import '~node_modules/modern-normalize/modern-normalize.css';
body {
  font-family:'Roboto', sans-serif;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
h1,
h2,
h3,
h4,
h5,
h6,
ul,
p
{
  margin: 0;
  }
.container {
  
    @media screen and (min-width: 768px) {
      
    }
    padding-left: 20px;
    padding-right: 20px;
    margin-left: auto;
    margin-right: auto;
  
    @media screen and (min-width: 320px) {
      width: 320px;
    }
    @media screen and (min-width: 768px) {
      width: 768px;
      padding-left: 32px;
      padding-right: 32px;
    }
    @media screen and (min-width: 1280px) {
      width: 1280px;
    }
  }
  .this {
    color: #ffffff;
    background: #000000;
  }